'use client';

import { useState } from 'react';
import Image from 'next/image';
import { Calendar, DollarSign, Loader2, Info, CheckCircle, Circle, Dot } from 'lucide-react';

import type { BucketListItemType } from '@/lib/types';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogFooter,
} from '@/components/ui/dialog';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { useToast } from '@/hooks/use-toast';
import { suggestActivityTiming } from '@/ai/flows/suggest-activity-timing';
import { estimateActivityCost, type EstimateActivityCostOutput } from '@/ai/flows/estimate-activity-cost';
import { Input } from './ui/input';
import { Label } from './ui/label';
import { Badge } from './ui/badge';
import { Separator } from './ui/separator';

type BucketListItemCardProps = {
  item: BucketListItemType;
  onUpdateItem: (item: BucketListItemType) => void;
};

const statusConfig = {
    'To Do': { icon: <Circle className="mr-2 h-4 w-4 text-muted-foreground" />, color: 'bg-gray-400' },
    'In Progress': { icon: <Dot className="mr-2 h-4 w-4 text-blue-500" />, color: 'bg-blue-500' },
    'Completed': { icon: <CheckCircle className="mr-2 h-4 w-4 text-green-500" />, color: 'bg-green-500' },
};


export function BucketListItemCard({ item, onUpdateItem }: BucketListItemCardProps) {
  const { toast } = useToast();

  const [isTimingLoading, setIsTimingLoading] = useState(false);
  const [timingSuggestion, setTimingSuggestion] = useState<string | null>(null);

  const [isCostLoading, setIsCostLoading] = useState(false);
  const [costData, setCostData] = useState<EstimateActivityCostOutput | null>(null);
  const [costLocation, setCostLocation] = useState('');

  const handleTimingSuggest = async () => {
    setIsTimingLoading(true);
    setTimingSuggestion(null);
    try {
      const result = await suggestActivityTiming({ activity: item.activity });
      setTimingSuggestion(result.bestTime);
    } catch (error) {
      console.error('Failed to suggest timing:', error);
      toast({
        variant: 'destructive',
        title: 'Timing Suggestion Error',
        description: 'Could not get a timing suggestion. Please try again.',
      });
    } finally {
      setIsTimingLoading(false);
    }
  };

  const handleCostEstimate = async () => {
    if (!costLocation) {
        toast({
            variant: 'destructive',
            title: 'Location Required',
            description: 'Please enter a location to estimate the cost.',
        });
        return;
    }
    setIsCostLoading(true);
    setCostData(null);
    try {
      const result = await estimateActivityCost({ activity: item.activity, location: costLocation });
      setCostData(result);
    } catch (error) {
      console.error('Failed to estimate cost:', error);
      toast({
        variant: 'destructive',
        title: 'Cost Estimation Error',
        description: 'Could not get a cost estimate. Please try again.',
      });
    } finally {
      setIsCostLoading(false);
    }
  };

  const handleStatusChange = (status: BucketListItemType['status']) => {
    onUpdateItem({ ...item, status });
  };
  
  const aiHint = item.activity.split(" ").slice(0, 2).join(" ").toLowerCase();

  return (
    <Card className="flex flex-col h-full overflow-hidden shadow-md hover:shadow-xl transition-shadow duration-300">
      <CardHeader>
        <div className="relative w-full h-48 rounded-t-lg overflow-hidden mb-4">
            <Image
                src={`https://placehold.co/600x400.png`}
                alt={item.activity}
                data-ai-hint={aiHint}
                layout="fill"
                objectFit="cover"
                className="transition-transform duration-300 group-hover:scale-105"
            />
        </div>
        <CardTitle className="text-xl font-bold">{item.activity}</CardTitle>
        <CardDescription>{item.description}</CardDescription>
      </CardHeader>
      <CardContent className="flex-grow">
      </CardContent>
      <CardFooter className="flex flex-col sm:flex-row items-stretch sm:items-center justify-between gap-2 bg-muted/50 p-4">
        <Select value={item.status} onValueChange={handleStatusChange}>
          <SelectTrigger className="w-full sm:w-[150px]">
            <SelectValue placeholder="Set status" />
          </SelectTrigger>
          <SelectContent>
            {Object.keys(statusConfig).map(status => (
                <SelectItem key={status} value={status}>
                    <div className="flex items-center">
                        {statusConfig[status as keyof typeof statusConfig].icon}
                        {status}
                    </div>
                </SelectItem>
            ))}
          </SelectContent>
        </Select>

        <div className="flex gap-2 w-full sm:w-auto">
          <Dialog>
            <DialogTrigger asChild>
              <Button variant="outline" className="flex-1" onClick={handleTimingSuggest}>
                <Calendar className="h-4 w-4 mr-2" />
                Timing
              </Button>
            </DialogTrigger>
            <DialogContent>
              <DialogHeader>
                <DialogTitle>Best Time to {item.activity}</DialogTitle>
                <DialogDescription>AI-powered suggestion for the best time of year.</DialogDescription>
              </DialogHeader>
              <div className="mt-4">
                {isTimingLoading && <Loader2 className="h-8 w-8 mx-auto animate-spin text-primary" />}
                {timingSuggestion && <p className="text-base bg-secondary p-4 rounded-md"><Info className="inline-block h-4 w-4 mr-2 text-primary" />{timingSuggestion}</p>}
              </div>
            </DialogContent>
          </Dialog>

          <Dialog>
            <DialogTrigger asChild>
                <Button variant="outline" className="flex-1">
                    <DollarSign className="h-4 w-4 mr-2" />
                    Cost
                </Button>
            </DialogTrigger>
            <DialogContent>
              <DialogHeader>
                <DialogTitle>Estimate Cost for {item.activity}</DialogTitle>
                <DialogDescription>Enter a location to get an AI-powered cost estimate.</DialogDescription>
              </DialogHeader>
              <div className="grid gap-4 py-4">
                <div className="grid grid-cols-4 items-center gap-4">
                  <Label htmlFor="location" className="text-right">Location</Label>
                  <Input id="location" value={costLocation} onChange={(e) => setCostLocation(e.target.value)} placeholder="e.g., Paris, France" className="col-span-3" />
                </div>
              </div>
              <DialogFooter>
                <Button onClick={handleCostEstimate} disabled={isCostLoading}>
                  {isCostLoading ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
                  Estimate Cost
                </Button>
              </DialogFooter>
              {costData && (
                 <div className="mt-4 space-y-4 pt-4 border-t">
                    <div className="flex justify-between items-center">
                        <p className="font-semibold text-lg">Estimated Total</p>
                        <Badge variant="secondary" className="text-lg">{costData.estimatedCost}</Badge>
                    </div>
                    <Separator />
                    <p className="font-semibold">Cost Breakdown:</p>
                    <p className="text-sm text-muted-foreground whitespace-pre-wrap bg-secondary p-4 rounded-md">{costData.costBreakdown}</p>
                 </div>
              )}
            </DialogContent>
          </Dialog>
        </div>
      </CardFooter>
    </Card>
  );
}
