'use client';

import { useState, useMemo } from 'react';
import type { BucketListItemType } from '@/lib/types';
import { BucketListItemCard } from './bucket-list-item-card';
import { Input } from './ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from './ui/select';
import { ListFilter, Search, ArrowDownAz, ArrowUpAz } from 'lucide-react';
import { Button } from './ui/button';

type BucketListProps = {
  items: BucketListItemType[];
  onUpdateItem: (item: BucketListItemType) => void;
};

export function BucketList({ items, onUpdateItem }: BucketListProps) {
  const [statusFilter, setStatusFilter] = useState('all');
  const [searchTerm, setSearchTerm] = useState('');
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('asc');

  const filteredItems = useMemo(() => {
    return items
      .filter((item) => {
        if (statusFilter === 'all') return true;
        return item.status === statusFilter;
      })
      .filter((item) =>
        item.activity.toLowerCase().includes(searchTerm.toLowerCase())
      );
  }, [items, statusFilter, searchTerm]);

  const sortedItems = useMemo(() => {
    return [...filteredItems].sort((a, b) => {
      if (sortOrder === 'asc') {
        return a.activity.localeCompare(b.activity);
      } else {
        return b.activity.localeCompare(a.activity);
      }
    });
  }, [filteredItems, sortOrder]);

  return (
    <div className="mt-12">
      <div className="flex flex-col md:flex-row gap-4 mb-8 p-4 bg-card rounded-lg shadow-sm border">
        <div className="flex-1 relative">
          <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-5 w-5 text-muted-foreground" />
          <Input
            placeholder="Search activities..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="pl-10"
          />
        </div>
        <div className="flex gap-4">
          <div className="flex items-center gap-2">
            <ListFilter className="h-5 w-5 text-muted-foreground" />
            <Select value={statusFilter} onValueChange={setStatusFilter}>
              <SelectTrigger className="w-[180px]">
                <SelectValue placeholder="Filter by status" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Statuses</SelectItem>
                <SelectItem value="To Do">To Do</SelectItem>
                <SelectItem value="In Progress">In Progress</SelectItem>
                <SelectItem value="Completed">Completed</SelectItem>
              </SelectContent>
            </Select>
          </div>
          <Button
            variant="outline"
            size="icon"
            onClick={() => setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc')}
            aria-label={`Sort ${sortOrder === 'asc' ? 'descending' : 'ascending'}`}
          >
            {sortOrder === 'asc' ? <ArrowDownAz className="h-5 w-5" /> : <ArrowUpAz className="h-5 w-5" />}
          </Button>
        </div>
      </div>

      {sortedItems.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {sortedItems.map((item) => (
            <BucketListItemCard key={item.id} item={item} onUpdateItem={onUpdateItem} />
          ))}
        </div>
      ) : (
        <div className="text-center py-16 px-4 bg-card rounded-lg">
            <h3 className="text-xl font-semibold">No Matching Activities</h3>
            <p className="text-muted-foreground mt-2">Try adjusting your search or filter settings.</p>
        </div>
      )}
    </div>
  );
}
